* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: Arial, sans-serif;
  height: 300vh;
  background: linear-gradient(to bottom, #001f3f, #0074d9);
}

.loader {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #001f3f, #0074d9);
  width: 100%;
  height: 100vh;
  z-index: 99;
}

.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.main {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.tree_image {
  position: absolute;
  width: 100%;
  height: 100vh;
  pointer-events: none;
}

.card {
  width: 40%;
  color: white;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.164);
  border-radius: 30px;
}

.card:not(.front) {
  opacity: 0;
}

.title {
  margin-bottom: 10px;
}

.skills-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.gif {
  filter: invert();
}

a {
  text-decoration: none;
  color: rgb(32, 129, 84);
}

.skill-Icon {
  width: 20px;
  height: 20px;
}

.snowflake {
  position: absolute;
  z-index: 5;
  top: -10%;
  animation: fall var(--flake-duration) linear infinite;
  width: var(--flake-size);
  left: var(--flake-position-x);
  will-change: transform, opacity;
}

.toggle-snow {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1000;
  margin: 5px 10px;
  color: #FFF;
}

.toggle-snow-button {
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #FFF;
  padding: 10px 10px;
  margin-top: 10px;
}

@keyframes fall {
  0% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateY(110vh);
    opacity: 0;
  }
}

.tree_image.front {
  filter: saturate(1) brightness(1);
  z-index: 3;
}

.tree_image.middle {
  filter: saturate(0.8) brightness(1.3);
  z-index: 2;
}

.tree_image.back {
  filter: saturate(0.6) brightness(1.6);
  z-index: 1;
}

.card.front {
  z-index: 2;
}

.card.middle {
  z-index: 1;
}

.video {
  width: 100%;
}

/* screens (phones in portrait mode) */
@media (max-width: 480px) {
  .skills-container {
    flex-direction: column;
  }

  .card {
    width: 70%;
    padding: 10px;
    font-size: 0.8rem;
  }

  .video {
    width: 100%;
    height: auto;
  }

  .card.front {
    z-index: 10;
  }
}